// float
.l {
	float: left;
}
.r {
	float: right;
}

// clear
.cb{
	clear: both;
}
.cl{
	clear: left;
}
.cr{
	clear: right;
}
.cfx{
	&:after,
	&:before{
		content: '';
		display: table;
		clear: both;
	}
}
.ctx{
	display: table-cell;
	&:after{
		clear: both;
		display: block;
		visibility: hidden;
		overflow: hidden;
		height: 0px !important;
		line-height: 0px;
		font-size: xx-large;
		content: " x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x ";
	}
}

// hide
.hide,
.js .jsHide {
	display: none;
}
.out,
.js .jsOut {
	position: absolute;
	left: -5000px;
	top: -5000px;
}
.vhide {
	position: absolute;
	height: 1px;
	width: 1px;
	overflow: hidden;
	border: 0;
	clip: rect(0 0 0 0);
	margin: -1px;
	padding: 0;
}

// align
.left {
	text-align: left;
}
.right {
	text-align: right;
}
.center {
	text-align: center;
}

// transform
.lower {
	text-transform: lowercase;
}
.upper {
	text-transform: uppercase;
}

// weight
.bold{
	font-weight: bold;
}
.thin{
	font-weight: normal;
}

// style
.italic{
	font-style: italic;
}
.normal{
	font-style: normal;
}

// valign
.top{
	vertical-align: top;
}
.middle{
	vertical-align: middle;
}
.bottom{
	vertical-align: bottom;
}

// size
.big {
	font-size: 18px;
	line-height: (22em/18);
	@media(min-width: $tablet){
		font-size: 24px;
		line-height: (28em/24);
	}
}
.medium {
	font-size: 20px;
}
.small {
	font-size: 14px;
	//line-height: (18em/14);
}

// color
.red {
	color: $red;
}
.orange {
	color: $orange;
}
.green {
	color: $green;
}
.light {
	color: $light;
}
.dark {
	color: $dark;
}
.primary {
	color: $primary;
}
.muni {
	color: $muni;
}
.white {
	color: #fff;

	h1, h2, h3, h4, h5, h6, p, a, cite, cite strong {
		color: $whiteInvertable;
	}

	a {
		transition: opacity .2s;
		.no-touch &:hover{
			color: #fff;
			opacity: 0.8;
		}
	}

	a:visited {
		color: #fff;
	}

	li:not([class]):before {
		background: #fff;
	}
}
.white ul, ul.white {
	li {
		&:before {
			background: #fff;
		}
	}
}
.white ol, ol.white {
	li {
		&:before {
			color: #fff;
		}
	}
}

// font
.font-primary{
	font-family: $font;
}
.font-muni {
	font-family: $fontMuni;
}
.font-consola{
	font-family: $fontConsola;
}

.title-highlighted {
	position: relative;
	padding-bottom: 20px;
	margin-bottom: 15px;
	&:before{
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 40px;
		height: 2px;
		margin-left: -20px;
		background: $border;
	}
	&.white {
		&:before{
			background: #fff;
		}
	}
}

// nowrap
.nowrap {
	white-space: nowrap;
}

