$linkedin: #007bb5;
$instagram: #3f729b;
$mail: #999;
$google: #dd4b39;
$youtube: #cd201f;
$github: #000;
$rss: #ffa333;

$primary: $muni; //new
$primaryColor: #fff;

$input: #aaa; //new
$separator: #ddd;

$link: $primary; //new
