$linkHover: $color; //new

$border: $greyDark; //new
$borderLight: rgba($greyDark, .5); //new
$borderPrimary: #ccd4e4;
$borderPrimaryLight: #e5e9f1;
$background: $greyLight; //new
$backgroundPrimary: lighten($borderPrimaryLight, 4%);

// Color variations of faculties
$elementsHover: rgba($muni, .05);
$whiteInvertable: #fff;
$menuHover: $dark;
$boxSeparator: $border;
$selectBg: url(#{$img}bg/select.png);
$selectSmallBg: url(#{$img}bg/select-small.png);

$menuSecondary: $primary;
$menuSecondaryLink: $whiteInvertable;

$mMargin: 50px;
