// Fonts
$font: Arial, Helvetica Neue, sans-serif;
$fontSecondary: $font;
$fontMuni: 'Muni', Arial, Helvetica Neue, sans-serif;
$fontItalic: Georgia, Times, 'Times New Roman', serif;
$fontConsola: Consolas, monaco, monospace;

// Base font size and line height
$fontSizeMobile: 16px;
$fontSize: 18px;
$fontSizeSmall: 14px;
$fontSizeSmaller: 12px;
$lineHeight: 1.45;

// Media queries
$retina: '(-webkit-min-device-pixel-ratio: 2), (min--moz-device-pixel-ratio: 2), (-o-min-device-pixel-ratio: 2/1), (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx)';
$mobileL: 480px;
