.row-main {
	@include clearfix();
	position: relative;
	margin: 0;
	padding: 0;
	margin: 0 auto;
	max-width: 1240px;
	padding: 0 15px;
	.row-main {
		padding: 0;
	}
}
.row{
	@include clearfix();
	position: relative;
	margin: 0;
	padding: 0;
}
.grid{
	@extend %reset-ol;
	@extend %grid;
	margin-left: -20px;
	&__cell{
		@extend %reset-ol-li;
		@extend %grid__cell;
		width: 100%;
		position: relative;
		border-left: 20px solid transparent;
		&.size--auto{
			width: auto;
		}
	}
	&--scroll{
		@extend %grid--scroll;
	}
	&--center{
		text-align: center;
	}
	&--right{
		text-align: right;
	}
	&--middle > &__cell{
		vertical-align: middle;
	}
	&--bottom > &__cell{
		vertical-align: bottom;
	}
	&--nogutter{
		margin-left: 0;
	}
	&--nogutter > &__cell{
		border-left-width: 0;
	}
}

@include grid('.size--m--', '-', 4, 'width');
@include grid('.push--m--', '-', 4, 'left');
@include grid('.pull--m--', '-', 4, 'left', -1);
@include grid('.size--m--', '-', 12, 'width');
@include grid('.push--m--', '-', 12, 'left');
@include grid('.pull--m--', '-', 12, 'left', -1);
@include grid('.size--l--', '-', 12, 'width');

$s: '.crossroad-links';

.crossroad-links{
	margin: 0 0 $mMargin;
	&__title{
		margin: 0 0 .75em;
	}
	&__list{
		margin: 0 0 $mMarginSmall;
		#{$s}{
			&__list{
				margin: .75em 0 0;
			}
		}
	}
	&__item{
		width: 100%;
		padding: 0 0 0 18px;
		margin: 0 0 .5em;
		position: relative;
		&:before {
			content: '' !important;
			position: absolute;
			left: 0;
			top: .55em !important;
			width: 4px;
			height: 4px;
			border-radius: 3px;
			background: #000;
			-webkit-print-color-adjust: exact;
			print-color-adjust: exact;
		}
	}
	&__subtitle{
		font-size: 18px;
		margin: 0 0 1em;
	}
	&__links{
		font-size: 14px;
		margin: 0 0 2px -15px;
		a{
			margin-left: 15px;
		}
	}
	&__desc{
		.table-vcard{
			margin: 0 0 1.25em;
			font-size: 14px;
			td{
				color: black;
			}
		}
	}
	&--type-2{
		#{$s}__link{
			font-weight: bold;
		}
		#{$s}__desc{
			display: block;
			font-size: 14px;
		}
	}
	&--type-4{
		list-style-type: none;
		#{$s}__item{
			display: inline-block;
			vertical-align: middle;
			padding-left: 0;
			margin: 0 0 1em;
			&:before{
				display: none;
			}
		}
		#{$s}__subtitle{
			margin: 0 0 5px;
		}
		#{$s}__desc{
			display: block;
			font-size: 14px;
			margin: 0;
		}
	}
	&--size--auto {
		#{$s}__list{
			margin: 0 0 0 -20px;
		}
		#{$s}__item{
			width: auto;
			margin-left: 20px;
		}
	}
	&--size--l--2-12,
	&--size--l--3-12,
	&--size--l--4-12,
	&--size--l--6-12{
		#{$s}__list{
			@include column-gap(30px);
		}
	}
	&--size--l--2-12{
		#{$s}__list{
			@include column-count(6);
		}
	}
	&--size--l--3-12{
		#{$s}__list{
			@include column-count(4);
		}
	}
	&--size--l--4-12{
		#{$s}__list{
			@include column-count(3);
		}
	}
	&--size--l--6-12{
		#{$s}__list{
			@include column-count(2);
		}
	}
}

$s: '.datebox';

.datebox {
	display: inline-block;
	vertical-align: top;
	position: relative;
	min-width: 70px;
	font-size: 16px;
	font-weight: bold;
	&__date {
		font-family: $fontMuni;
		margin: 0;
	}
}

$s: '.meta';

.meta{
	font-size: 0px;
	margin: 0 0 10px;
	+ *{
		margin-top: 0;
	}
	&__item{
		padding: 0 5px;
		font-size: $fontSizeSmall;
		display: inline-block;
		vertical-align: middle;
		&:first-child{
			padding-left: 0;
		}
	}
	&--block {
		position: relative;
		margin-top: 15px;
		padding-top: 15px;
		&:first-child {
			margin-top: 0;
			padding-top: 0;
		}
		#{$s}__item {
			display: block;
			padding: 0;
			border-left: 0;
		}
	}
	&--inline {
		#{$s}__item {
			display: inline;
			padding: 0;
			border-left: 0;
			&:after {
				content: ', ';
			}
			&:last-child:after {
				content: '';
			}
		}
	}
}

$s: '.image';

.image {
	position: relative;
	display: block;
	box-sizing: border-box;
	margin-bottom: $mMarginSmall;
	clear: both;
	&:before {
		content: '';
		display: table;
		clear: both;
	}
	img {
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto;
	}
	&__holder {
		position: relative;
		display: block;
		&:after {
			content: '';
			display: block;
			padding-top: 56.25%;
		}
	}
	&__inner {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: 0;
		line-height: 1;
		&:before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			height: 100%;
		}
	}
	&__caption {
		margin-top: 20px;
		font-size: $fontSizeSmall;
		line-height: (20 / 14);
		font-style: italic;
		color: #555;
		&--inside {
			position: absolute;
			left: 15px;
			right: 15px;
			bottom: 10px;
			margin: 0;
			color: white;
			font-size: 12px;
			font-style: normal;
		}
	}
	&--4by3 {
		#{$s}__holder:after {
			padding-top: 75%;
		}
	}
	&--full {
		img {
			position: absolute;
			top: 50%;
			left: 50%;
			min-width: 100%;
			min-height: 100%;
			max-width: none;
			max-height: none;
		}
		#{$s}__holder:after {
			padding-top: 0;
			height: 600px;
		}
		#{$s}__inner {
			overflow: hidden;
		}
	}
	&--right {
		float: right;
		margin-left: $mMarginSmall;
		+ * {
			clear: left;
		}
	}
	&--left {
		float: left;
		margin-right: $mMarginSmall;
		+ * {
			clear: right;
		}
	}
}
