// Typo
body {
	font: normal #{$fontSizeMobile}/#{$lineHeight} $font;
	color: $color;
	min-width: 320px;
	background: $background;
	@media(min-width: $tablet){
		font-size: $fontSize;
	}
}

// Titles
h1,
h2,
h3,
h4,
h5,
h6,
.h0,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
	font-family: $fontSecondary;
	font-weight: bold;
	line-height: 1.11em;
	color: $dark;
	a{
		text-decoration: none;
	}
}
.h0 {
	margin: .75em 0;
	font-size: #{nth($h0Size, 1)};
	@media (min-width: $tablet) {
		font-size: #{nth($h0Size, 2)};
	}
	@media (min-width: $desktop) {
		font-size: #{nth($h0Size, 3)};
	}
}
h1,
.h1 {
	margin: .9em 0 .6em;
	font-size: #{nth($h1Size, 1)};
	@media (min-width: $tablet) {
		font-size: #{nth($h1Size, 2)};
	}
	@media (min-width: $desktop) {
		font-size: #{nth($h1Size, 3)};
	}
}
h2,
.h2 {
	margin: .95em 0 .7em;
	font-size: #{nth($h2Size, 1)};
	@media (min-width: $tablet) {
		font-size: #{nth($h2Size, 2)};
	}
	@media (min-width: $desktop) {
		font-size: #{nth($h2Size, 3)};
	}
}
h3,
.h3{
	margin: 1.5em 0 .7em;
	font-size: #{nth($h3Size, 1)};
	@media (min-width: $tablet) {
		font-size: #{nth($h3Size, 2)};
	}
	@media (min-width: $desktop) {
		font-size: #{nth($h3Size, 3)};
	}
}
h4,
.h4{
	margin: 1.55em 0 .8em;
	font-size: #{nth($h4Size, 1)};
	line-height: 1.2em;
	@media (min-width: $tablet) {
		font-size: #{nth($h4Size, 2)};
	}
	@media (min-width: $desktop) {
		font-size: #{nth($h4Size, 3)};
	}
}
h5,
.h5{
	margin: 1.55em 0 .5em;
	font-size: $h5Size;
	line-height: 1.2em;
}
h6,
.h6{
	margin: 1.55em 0 .7em;
	font-size: $h6Size;
	line-height: 1.2em;
	text-transform: uppercase;
}

// Paragraph
p {
	margin: 0 0 2.1em;
}

// Separator
hr {
	border: none;
	margin: $mMarginSmall 0;
	height: 1px;
	border-top: 1px solid $border;
	overflow: hidden;
}

// Blockquote
blockquote {
	margin: 0 0 1.25em;
	padding: 0;
}

// Links
a {
	color: $link;
	text-decoration: underline;
	text-underline-position: auto;
	text-decoration-skip: ink;
	transition: color .2s;
	@include hover() {
		&:hover {
			color: $linkHover;
		}
	}
	&:not([href]){
		color: $color;
		cursor: default;
		text-decoration: line-through;
	}
	.no-a-underline & {
		text-decoration: none;
	}
}

// Lists
ul,
ol {
	margin: 0 0 2.1em;
	list-style: none;
	padding: 0;
}
li {
	padding: 0 0 0 15px;
	margin: 0 0 .25em;
	position: relative;
	ol,
	ul {
		margin-left: 20px;
		&,
		&:first-child{
			margin-top: .25em;
			margin-bottom: 0;
		}
	}
}
ul {
	> li {
		&:not([class]):before{
			content: '' !important;
			position: absolute;
			left: 0;
			top: .55em !important;
			width: 4px;
			height: 4px;
			border-radius: 3px;
			background: $dark;
		}
	}
}
ol {
	counter-reset: item;
	> li {
		background: none;
		padding: 0 0 0 (30/18)+em;
		&:before{
			content: counter(item)".";
			counter-increment: item;
			position: absolute;
			left: 0;
			//top: (10/18)+em;
			font-weight: bold;
			color: $dark;
			// font-size: (12/18)+em;
			width: 1.4em;
			text-align: right;
		}
	}
	&[type="spc"]{
		counter-reset: section;
		list-style-type: none;
		> li{
			padding: 0 0 0 (34/18)+em;
			&:before{
				content: counters(section, ".") " ";
				counter-increment: section;
			}
			ol {
				&[type="spc"]{
					> li{
						padding: 0 0 0 (34/18)+em;
						ol {
							&[type="spc"]{
								> li{
									padding: 0 0 0 (48/18)+em;
								}
							}
						}
					}
				}
			}
		}
	}
	&[type="1"] {
		> li {
			&:before {
				content: counter(item, decimal)".";
			}
		}
	}
	&[type="A"] {
		> li {
			&:before {
				content: counter(item, upper-alpha)".";
			}
		}
	}
	&[type="a"] {
		> li {
			&:before {
				content: counter(item, lower-alpha)".";
			}
		}
	}
	&[type="I"] {
		> li {
			&:before {
				content: counter(item, upper-roman)".";
			}
		}
	}
	&[type="i"] {
		> li {
			&:before {
				content: counter(item, lower-roman)".";
			}
		}
	}
}
p + ul {
	margin-top: -1em;
}
dl {
	margin: 0 0 2.1em;
}
dt {
	font-weight: bold;
	margin: 0;
}
dd {
	margin: 0 0 .75em;
	padding: 0;
}

// Tables
table {
	width: 100%;
	clear: both;
	margin: 0 0 2em;
	empty-cells: show;
	border-collapse: collapse;
	border: none;
	font-size: 16px;
	&.table-padded {
		td,
		th {
			&:first-child{
				padding-left: 10px;
			}
			&:last-child{
				padding-right: 10px;
			}
		}
	}
	&.table-condensed {
		width: auto;
		td,
		th {
			padding: 0 0 0 16px;
			border: 0;
		}
		td:first-child,
		th:first-child {
			padding-left: 0;
		}
	}
	&.table-big{
		td, th {
			padding-top: 15px;
			padding-bottom: 15px;
		}
	}

	@media(max-width: $mobileL - 1){
		display: block;
		tbody,
		tr{
			display: block;
		}
		th{
			display: block;
			border-bottom-width: 0px;
			padding-left: 0;
			padding-right: 0;
		}
		td{
			display: block;
			padding-top: 0;
			padding-left: 0;
			padding-right: 0;
			border-top-width: 0px;
			border-bottom-width: 0px;
			&:last-child{
				border-bottom-width: 1px;
			}
		}
	}
}
td,
th {
	vertical-align: top;
	padding: 10px 22px;
	border: 1px solid $borderLight;
	border-width: 0 0 1px;
	text-align: left;
	&:first-child{
		padding-left: 0;
	}
	&:last-child{
		padding-right: 0;
	}
}
th {
	font-weight: bold;
}
tbody{
	tr:last-child{
		td,
		th{
			border-bottom: 0;
		}
	}
}
thead{
	font-weight: bold;
	th,
	td{
		padding: 15px 22px;
		border-width: 0 0 1px;
		border-color: $border;
	}
}
tfoot{
	font-weight: bold;
	font-size: 16px;
	th,
	td{
		padding: 15px 22px;
		border-color: $border;
		border-width: 1px 0 0;
	}
}
.table-wrap{
	background: #fff;
	border: 1px solid $border;
	padding: 10px 22px;
	margin: 0 0 1.25em;
	table{
		margin: 0;
	}
}
.js .tr-clickable{
	cursor: pointer;
	td,
	th{
		transition: color .2s, background .2s;
		color: $primary;
		a {
			text-decoration: none;
		}
	}
	@include hover() {
		&:hover {
			td,
			th{
				background: #fff;
			}
		}
	}
}

.js .tr-clickable-light{
	@include hover() {
		&:hover {
			td,
			th{
				background: $greyLight;
			}
		}
	}
}

// Images
img.left{
	float: left;
	margin: .25em 30px 10px 0;
}
img.right{
	float: right;
	margin: .25em 0 10px 30px;
}
@media(max-width: $desktopWide - 1){
	img{
		max-width: 100%;
		height: auto;
	}
}
.image,
img{
	~ ul,
	~ ol{
		li{
			overflow: hidden;
		}
	}
}

// Acts
.act {
	h2, .h2 {
		font-size: $h5Size;
		text-align: center;
	}
	h3, .h3 {
		text-align: center;
		margin-top: 5px;
		font-size: #{nth($h3Size, 1)};
		@media (min-width: $tablet) {
			font-size: #{nth($h3Size, 2)};
		}
	}
	ol {
		margin-left: 0;
		li {
			padding-left: 35px;
		}
		> li {
			&:before{
				content: "("counter(item)")";
				font-size: inherit;
				font-weight: normal;
				top: 0;
			}
			li {
				&:before{
					content: counter(item, lower-alpha)")";
				}
				li {
					&:before{
						content: counter(item, lower-roman)".";
					}
				}
			}
		}
	}
}
