$s: '.box-hero';

.box-hero {
	position: relative;
	display: block;
	margin: 0 0 50px;
	border: 1px solid transparent;
	text-decoration: none;
	color: black;
	background: white;
	.row-main {
		position: static;
	}
	&__img {
		position: relative;
		top: -1px;
		margin-left: -16px;
		margin-right: -16px;
		overflow: hidden;
		&:before {
			content: '';
			display: block;
			padding-top: 300px;
		}
		img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			max-width: none;
			min-height: 100%;
			min-width: 100%;
			width: auto;
			height: auto;
		}
	}
	// content
	&__content {
		display: table;
		table-layout: fixed;
		width: 100%;
		position: relative;
		z-index: 1;
	}
	&__content-inner {
		display: table-cell;
		vertical-align: middle;
		padding: 20px 0px 25px;
		> * {
			max-width: 600px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		> .center{
			margin-left: auto;
			margin-right: auto;
		}
	}
	// in row-main
	.row-main & &__content-inner {
		padding-left: 20px;
		padding-right: 20px;
	}
	.row-main & &__img {
		margin-left: -1px;
		margin-right: -1px;
	}
	// particles
	&__particles {
		display: none;
		position: absolute;
		top: 0;
		left: 50%;
		right: 0;
		bottom: 0;
		overflow: hidden;
	}
	&__img ~ &__particles {
		right: 50%;
		margin-left: -310px;
	}
	// VARIANTS
	&--space {
		margin: $mMargin 0;
	}
	&--bordered {
		border-color: #bbb;
	}
	// image
	&__img {
		position: absolute;
		left: 50%;
		top: -1px;
		bottom: -1px;
		right: -1px;
		height: auto;
		overflow: hidden;
		margin: 0;
	}
	// content
	&__content-inner {
		padding: 45px 0;
	}
	&__img ~ &__content {
		padding: 0;
		max-width: 50%;
	}
	&__img ~ &__content &__content-inner {
		padding: 30px 30px 30px 0;
	}
	// in row-main
	.row-main & &__content-inner {
		padding-left: 30px;
	}
	.row-main & &__img {
		margin: 0;
	}
	img left
	&--inverse{
		#{$s}{
			&__img{
				left: 0;
				right: 50%;
			}
		}
		.box-hero__content{
			margin-left: auto;
		}
		.box-hero__content .box-hero__content-inner {
			padding-left: 70px;
			padding-right: 0;
		}
		.row-main & .box-hero__content .box-hero__content-inner {
			padding-right: 60px;
		}
	}
	// VARIANTS
	&--size-s &__content-inner {
		height: 300px;
	}
	&--size-m &__content-inner {
		height: 450px;
	}
	&--size-l &__content-inner {
		height: 530px;
	}
	&--particles &__particles {
		display: block;
	}
	&__img ~ &__content &__content-inner {
		padding: 60px 70px 60px 0;
	}
	.row-main & &__content-inner {
		padding-left: 60px;
	}
}

$s: '.box-vcard';

.box-vcard {
	margin: 0 0 $mMargin;
	.table-vcard {
		clear: none;
		th,
		td {
			border: none;
			padding: 2px 10px 2px 0;
		}
	}
	&__inner {
		display: block;
		text-decoration: none;
		color: black;
		padding: 30px 0 30px 200px;
		@include clearfix();
	}
	&__img{
		float: left;
		margin: 0 0 0 -170px;
		width: 140px;
		padding-right: 20px;
	}
	&__name {
		font-size: 21px;
		margin-bottom: 0;
		text-align: left;
	}
	&__position{
		font-size: 16px;
		text-align: left;
	}
	&__content {
		display: table-cell;
		&:after{
			clear: both;
			display: block;
			visibility: hidden;
			overflow: hidden;
			height: 0px !important;
			line-height: 0px;
			font-size: xx-large;
			content: " x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x ";
		}
		hr {
			margin: 25px 0;
			border-top-color: #eee;
		}
		strong {
			color: black;
		}
		ul {
			padding: 0;
			margin: 0;
		}
		li {
			margin: 0;

			&:before {
				top: .7em;
			}
		}
	}
	&__foot {
		background: #eee;
		margin: 0 -30px -30px -200px;
		padding: 20px 30px;
		> *:last-child {
			margin-bottom: 0;
		}
	}
	&--simple,
	&--compact {
		#{$s}__name {
			color: black;
			font-size: 24px;
			text-align: left;
			margin-bottom: 5px;
		}
		#{$s}__position {
			text-align: left;
		}
		#{$s}__inner {
			display: table;
			width: 100%;
			padding: 0;
			> * {
				display: table-cell;
				vertical-align: middle;
			}
		}
		#{$s}__img {
			width: 105px;
			float: none;
			margin: 0;
			padding: 0;
			vertical-align: top;
			img{
				max-width: none;
			}
		}
		#{$s}__content {
			padding: 30px;
			> *:last-child {
				margin-bottom: 0;
			}
		}
	}
	&--compact {
		.table-vcard {
			font-size: 14px;
		}
		#{$s}__name {
			font-size: 18px;
		}
		#{$s}__position {
			font-size: 14px;
			margin-bottom: 10px;
		}
		#{$s}__content {
			padding: 15px 30px;
		}
	}
	&--noimg {
		#{$s}__inner {
			padding-left: 30px;
		}
		#{$s}__foot {
			padding-left: 30px;
			margin-left: -30px;
		}
	}
	&--big {
		#{$s}__inner {
			padding-left: 300px;
		}
		#{$s}__img {
			margin: 0 0 0 -270px;
			width: 210px;
		}
		#{$s}__foot {
			padding-left: 300px;
			margin-left: -300px;
		}
	}
}

$s: '.box-testimonial';

.box-testimonial{
	margin: 0 0 $mMargin;
	padding: 20px 0;
	border: 2px solid $color;
	border-width: 2px 0;
	&:after{
		content: '';
		display: table;
		clear: both;
	}
	> :last-child{
		margin-bottom: 0;
	}
	&__comment{
		font-size: 18px;
		color: black;
		font-weight: bold;
	}
	&__author{
		font-size: 14px;
		font-style: normal;
		display: block;
		font-family: $font;
		margin: 0 0 1.25em;
		a{
			text-decoration: none;
			color: black;
			display: inline-block;
			vertical-align: top;
		}
		strong{
			font-size: 16px;
			color: black;
		}
		&.with-img{
			position: relative;
			min-height: 46px;
			padding: 8px 0 8px 80px;
			a{
				margin-left: -80px;
				padding-left: 80px;
			}
			#{$s}__author__img{
				position: absolute;
				left: 0;
				top: 0;
				width: 62px;
				text-align: center;
				img{
					border-radius: 100px;
				}
			}
		}
	}
	&--avatar{
		padding-left: 300px;
		#{$s}__img{
			float: left;
			margin-left: -300px;
			width: 180px;
			img{
				border-radius: 300px;
			}
		}
		#{$s}__comment{
			font-size: 24px;
		}
	}
	&--img{
		position: relative;
		.row-main{
			&:before{
				content: '';
				height: 490px;
				display: inline-block;
				vertical-align: middle;
				margin-left: -.2em;
			}
		}
		#{$s}__content{
			padding: 20px 15px 0 0;
			width: 50%;
			display: inline-block;
			vertical-align: middle;
		}
		#{$s}__comment{
			font-size: 24px;
		}
		#{$s}__img{
			margin: 0 0 0 15px;
			position: absolute;
			left: 50%;
			top: 0;
			bottom: 0;
			right: 0;
			overflow: hidden;
			img{
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				max-width: none;
				min-height: 100%;
				width: auto;
			}
		}
	}
	&--bordered,
	&--background{
		&#{$s}--avatar{
			padding: 30px 50px 40px 280px;
			#{$s}__img{
				margin-left: -230px;
				width: 180px;
			}
		}
	}
}

$s: '.box-article-grid';

.box-article-grid{
	margin: 0 0 $mMargin;
	display: block;
	position: relative;
	font-size: 14px;
	&__inner {
		color: $color;
		text-decoration: none;
		&[href]{
			#{$s}__title {
				color: $muni;
			}
		}
	}
	&__content{
		padding: 20px 0;
		> :last-child{
			margin-bottom: 0;
		}
	}
	&__title{
		font-size: 18px;
		margin: .5em 0;
		position: relative;
		padding-bottom: 15px;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
	}
	&__img{
		margin: 0;
		text-align: center;
		overflow: hidden;
		img {
			min-width: 100%;
		}
	}

	&--noimg{
		#{$s}__content{
			padding-top: 0;
		}
	}
}

$s: '.box-article';

.box-article{
	margin: 0 0 $mMargin;
	font-size: 14px;
	display: block;
	padding-left: 285px;
	@include clearfix();
	&__inner[href]{
		color: $color;
		text-decoration: none;
	}
	&__img{
		float: left;
		text-align: center;
		margin: 0 0 0 -285px;
		width: 270px;
	}
	&__title{
		font-size: 18px;
		margin: 0 0 .8em;
		color: $link;
		position: relative;
		padding-bottom: 15px;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
	}
	&__desc{
		margin: 0;
	}
}

$s: '.box-article-list';

.box-article-list{
	margin: 0 0 $mMargin;
	display: block;
	position: relative;
	background: #fff;
	&__inner[href]{
		color: $color;
		text-decoration: none;
	}
	ul{
		margin: 0 0 1.5em;
	}
	&__content{
		border: 1px solid $border;
		border-width: 1px 0px 1px 1px;
		padding: 20px;
		width: 50%;
		min-height: 300px;
		> :last-child{
			margin-bottom: 0;
		}
	}
	&__desc{
		font-size: 14px;
	}
	&__title{
		color: $link;
		font-size: 24px;
		margin: .5em 0;
		position: relative;
		padding-bottom: 15px;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
	}
	&__img{
		position: absolute;
		left: 50%;
		top: 0;
		bottom: 0;
		right: 0;
		overflow: hidden;
		margin: 0;
		img{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			max-width: none;
			min-height: 100%;
			width: auto;
		}
	}

	&__more{
		font-size: 16px;
		font-weight: bold;
		font-family: $fontMuni;
		color: $muni;
	}

	&--noimg{
		#{$s}__content{
			border-width: 1px;
			width: auto;
			min-height: 1px;
		}
	}
}

$s: '.box-event';

.box-event {
	margin: 0 0 $mMargin;
	.datebox {
		margin-bottom: 12px;
	}
	&__title {
		margin-top: 0;
		font-size: 18px;
		line-height: (22/18);
		position: relative;
		padding-bottom: 15px;
		margin-bottom: 15px;
		color: $muni;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 35px;
			height: 2px;
			background: $color;
		}
		a {
			display: block;
		}
	}
	&__label {
		display: inline-block;
		vertical-align: top;
		padding: 3px 12px 4px 11px;
		background: $muni;
		color: $whiteInvertable;
		font-size: $fontSizeSmaller;
		font-weight: 700;
		&-wrap {
			margin-top: (12em/16);
		}
	}
	&__content{
		font-size: 14px;
		line-height: 1.45;
		text-decoration: none;
		color: $color;
		display: block;
		> :last-child{
			margin-bottom: 0;
		}
	}

	&--side {
		padding-top: 0;
		.datebox {
			float: left;
			margin-top: 0;
			margin-left: -100px;
			margin-bottom: 0;
			text-align: right;
		}
		#{$s}__title {
			& ~ * {
				margin-bottom: 2px;
			}
		}
		#{$s}__content {
			padding-left: 100px;
			padding-bottom: 0;
			font-size: 16px;
			&:after {
				content: '';
				display: table;
				clear: both;
			}
		}
	}
}

.box-bg{
	margin: 0 0 $mMargin;
	padding: 15px;
	background: $greyLight;
	> .row-main > :last-child,
	> :last-child{
		margin-bottom: 0;
	}
	.menu-inside {
		&:before {
			display: none;
		}
	}
}

$s: '.box-alternative';

.box-alternative {
	position: relative;
	display: block;
	margin: 0 0 $mMargin;
	background: #eee;
	-webkit-print-color-adjust: exact;
	print-color-adjust: exact;
	page-break-inside: avoid;
	page-break-after: avoid;
	&__inner {
		display: block;
		color: $color;
		text-decoration: none;
	}
	&__img {
		display: none;
	}
	&__title {
		font-size: 18px;
		margin-bottom: 0;
		page-break-inside: avoid;
		page-break-after: avoid;
	}
	&__content {
		padding: 24px 20px 12px;
	}
	&__author {
		font-size: 16px;
		margin: 10px 0 0;
		position: relative;
		padding-top: 15px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 35px;
			height: 1px;
			background: currentColor;
		}
	}
	&__desc{
		position: relative;
		padding-bottom: 30px;
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 35px;
			height: 2px;
			background: currentColor;
		}
	}
}

.box-quote {
	text-align: center;
	padding: ($mMargin + 40) 0 $mMargin;
	margin-bottom: $mMargin;
	position: relative;
	border: 2px solid $color;
	border-width: 2px 0;
	.row-main{
		blockquote{
			padding: 0;
		}
	}
	blockquote {
		max-width: 980px;
		margin: 0 auto;
		position: relative;
		padding: 0 20px;
	}
	p {
		font-weight: bold;
		font-size: 30px;
		line-height: 34px;
		margin-bottom: 0;
	}
	cite {
		font-size: 16px;
		font-style: normal;
		display: inline-block;
		margin-top: 35px;
		padding-top: 25px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: -35px;
			width: 70px;
			height: 2px;
			background: rgba($color, .5);
		}
	}
}

$s: '.box-category';

.box-category{
	margin: 0 0 $mMargin;
	border: 1px solid transparent;
	text-align: center;
	display: block;
	padding: 55px 30px 45px;
	position: relative;
	background: #eee;
	background-image: none !important;
	-webkit-print-color-adjust: exact;
	print-color-adjust: exact;
	&__inner {
		text-decoration: none;
		color: $color;
	}
	p{
		margin-bottom: 30px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&__title{
		font-size: 24px;
		margin: 0 0 30px;
	}
}

$s: '.box-grid';

.box-grid{
	margin: 0 0 $mMargin;
	.grid {
		overflow: hidden;
	}
	&__inner{
		font-size: 14px;
		display: block;
		text-decoration: none;
		color: black;
		text-align: center;
		border: 1px solid #ccc;
		border-width: 1px 0 0 1px;
		padding: 20px 15px;
		margin: -2px 0 0 -2px;
		&:first-child {
			margin-top: -2px;
		}
		&--left {
			text-align: left;
		}
		> *:last-child {
			margin-bottom: 0;
		}
	}
	&:not(#{$s}--bordered):not(#{$s}--background) {
		.grid__cell:first-child {
			#{$s}__inner--left {
				padding-left: 2px;
			}
		}
	}
	&--background {
		padding: 30px 0;
		background-color: #ddd;
	}
}

.box-poll{
	margin: 0 0 $mMargin;
	&__question{
		font-weight: bold;
		color: black;
	}
	&__list{
		@extend %reset-ul;
		margin: 0 0 15px;
		border: 1px solid $borderPrimary;
	}
	&__item{
		@extend %reset-ul-li;
		border-top: 1px solid $borderPrimaryLight;
		font-weight: bold;
		&:first-child{
			border-top: none;
		}
	}
	&__inner{
		display: block;
		padding: 15px 40px 20px 20px;
		position: relative;
		color: black;
		text-decoration: none;
	}
	&__answer{
		margin: 0;
		padding: 0 0 0 25px;
		position: relative;
	}
	&__number{
		position: absolute;
		left: 0;
		top: 0;
		color: black;
	}
	&__icon{
		position: absolute;
		right: 20px;
		top: 50%;
		font-size: 20px;
		margin-top: -10px;
		color: black;
		opacity: 0;
	}
	&__percent{
		margin-right: 45px;
		position: relative;
		font-size: 14px;
		color: black;
		height: 10px;
		margin-top: 10px;
		&__inner{
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			background: $primary;
		}
		&__number{
			position: absolute;
			left: 100%;
			top: -5px;
			width: 45px;
			text-align: right;
			line-height: 20px;
			white-space: nowrap;
		}
	}
	&__info{
		margin: 0;
		font-size: 14px;
	}
}

.box-content {
	margin: 0 0 50px;
	max-width: 800px;
	li {
		overflow: hidden;
	}
}

$s: '.box-workplaces';

.box-workplaces {
	margin-bottom: $mMarginSmall;
	&__title {
		font-family: $fontMuni;
		margin-bottom: 25px;
		font-size: 24px;
	}
	&__wrap {
		border: 1px solid $border;
		padding: 60px 20px 0;
		+ #{$s}__wrap {
			margin-top: -1px;
		}
	}
}

$s: '.box-product';

.box-product{
	display: block;
	text-align: left;
	margin-bottom: $mMarginSmall;
	&__inner {
		text-decoration: none;
	}
	&__img{
		margin: 0;
		position: relative;
		text-align: center;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__labels{
		position: absolute;
		left: 10px;
		top: 15px;
		margin-bottom: -10px;
		.tag{
			float: left;
			clear: both;
			margin-bottom: 10px;
		}
	}
	&__content{
		padding: 10px 0 15px;
	}
	&__title{
		color: $muni;
		font-size: 16px;
		line-height: 1.2em;
		margin: .5em 0 15px;
		padding-bottom: 18px;
		position: relative;
		transition: color .2s;
		&:before{
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 35px;
			height: 2px;
			background: $color;
		}
	}
	&__author{
		display: block;
		color: $color;
		font-size: 16px;
		font-weight: 400;
		margin-top: 10px !important;
	}
	&__price{
		color: $color;
		font-size: 24px;
		font-weight: bold;
		font-family: $fontMuni;
		margin-bottom: 0;
	}
	&__stock{
		display: block;
		font-size: 14px;
		line-height: 1.2;
	}

	&--secondary{
		overflow: hidden;
		#{$s}__img{
			float: left;
			max-width: 145px;
		}
		#{$s}__content{
			padding: 0 0 0 160px;
		}
		#{$s}__labels{
			left: 5px;
			top: 10px;
		}
	}
}

.box-product-attrs {
	margin: 0 0 30px;
	&__list {
		@extend %reset-ul;
		@include inline-list(auto, 13px)
		margin: 0 0 -10px -10px;
	}
	&__item {
		min-width: 32px;
		@extend %reset-ul-li;
		text-transform: uppercase;
		background: #eee;
		border: 1px solid #ddd;
		text-align: center;
		margin: 0 0 10px 10px;
		padding: 0 5px;
		height: 32px;
		line-height: 32px;
	}
}

$s: '.box-timeline';

.box-timeline{
	margin: 0 0 $mMargin;
	p:last-child{
		margin-bottom: 0;
	}
	&__list{
		@extend %reset-ul;
		position: relative;
		&:before{
			content: '';
			position: absolute;
			left: 11px;
			top: 0;
			bottom: 0;
			width: 1px;
			background: $border;
			-webkit-print-color-adjust: exact;
			color-adjust: exact;
		}
		+ *{
			margin-top: $mMargin;
		}
	}
	&__item{
		@extend %reset-ul-li;
		margin-top: $mMarginSmall;
		overflow: hidden;
		&:first-child,
		&:last-child{
			#{$s}__point{
				&:before{
					content: '';
					position: absolute;
					left: 50%;
					width: 2px;
					height: 5000px;
					margin-left: -1px;
					background: $background;
				}
			}
		}
		&:first-child{
			#{$s}__point{
				&:before{
					bottom: 100%;
					margin-bottom: 4px;
				}
			}
		}
		&:last-child{
			#{$s}__point{
				&:before{
					top: 100%;
					margin-top: 2px;
				}
			}
		}
		&--end{
			#{$s}__point{
				background: $background;
				border: 1px solid $border;
				width: 13px;
				height: 13px;
				left: 5px;
				top: 5px;
			}
		}
	}
	&__inner{
		display: block;
		color: $color;
		text-decoration: none;
		padding: 0 0 0 35px;
		position: relative;
		&[href]{
			#{$s}__title{
				color: $link;
			}
		}
	}
	&__point{
		position: absolute;
		left: 2px;
		top: 4px;
		border: 3px solid $color;
		width: 19px;
		height: 19px;
		border-radius: 50%;
		background: $color;
	}
	&__title{
		display: block;
		font-size: 16px;
		color: $dark;
		font-family: $fontSecondary;
	}
	&__date{
		display: block;
		font-size: 16px;
	}
	&__title-date{
		color: #595959;
		font-size: 16px;
		margin: 0 0 15px 25px;
		padding-top: 20px;
	}
	&__box{
		border: 1px solid $border;
		background: #fff;
		padding: 15px;
		position: relative;
		&:before,
		&:after{
			content: '';
			position: absolute;
			right: 100%;
			top: 16px;
			border-width: 10px 10px 10px 0;
			border-style: dashed solid;
			border-color: transparent $border;
		}
		&:after{
			margin-right: -2px;
			border-color: transparent #fff;
		}
		&__title{
			font-size: 18px;
			margin-top: 0;
			&--noborder{
				padding-bottom: 0 !important;
				&:before{
					display: none;
				}
			}
			&--big{
				font-size: 24px;
			}
		}
		&__img {
			position: absolute;
			top: -1px;
			bottom: -1px;
			right: -1px;
			overflow: hidden;
			margin-bottom: 0;
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				max-width: none;
				min-width: 100%;
				min-height: 100%;
				width: auto;
				height: auto;
				transform: translate(-50%, -50%);
			}
		}
		#{$s}__point{
			top: 15px;
			left: -36px;
		}
	}

	&--center{
		text-align: center;
		#{$s}__list{
			font-size: 18px;
			font-family: $fontSecondary;
			font-weight: bold;
			&:before{
				left: 50%;
			}
			+ *{
				margin-top: $mMargin;
			}
		}
		#{$s}__item{
			margin-top: $mMargin;
			&--end{
				font-weight: normal;
				#{$s}__title,
				#{$s}__date{
					color: $color;
				}
				#{$s}__point{
					top: 8px;
					margin-left: -6px;
				}
			}
		}
		#{$s}__inner{
			padding: 0 50% 0 0;
			text-align: right;
			&[href]{
				color: $link;
				#{$s}__title,
				#{$s}__date {
					color: inherit;
				}
			}
		}
		#{$s}__point{
			top: 4px;
			left: 50%;
			margin-left: -9px;
		}
		#{$s}__title,
		#{$s}__date{
			font-size: 100%;
			color: $dark;
			-moz-transition: none;
		}
		#{$s}__title{
			padding-right: 25px;
		}
		#{$s}__date{
			position: absolute;
			left: 50%;
			top: 0;
			width: 50%;
			padding-left: 25px;
			text-align: left;
		}

	}

	&--boxes{
		font-size: 16px;
		#{$s}__list{
			margin: 0 -10px 0 0;
			padding: 0 10px 0 0;
			&:before {
				left: 9px;
			}
		}
		#{$s}__box__title{
			position: relative;
			padding-bottom: 15px;
			&:before{
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 40px;
				height: 2px;
				background: $color;
			}
		}
		#{$s}__inner{
			&[href]{
				#{$s}__box{
					&__title{
						color: $link;
					}
				}
			}
		}
		#{$s}__item{
			&:last-child{
				#{$s}__point{
					&:before{
						margin-top: 3px;
					}
				}
			}
		}
	}

	&--titles{
		#{$s}__box__title{
			font-family: $fontMuni;
		}
	}

	&--numbers{
		#{$s}__box{
			padding-bottom: 40px;
		}
		[data-number] {
			top: 14px;
			width: 20px;
			height: 20px;
			border-width: 2px;
			&:after{
				content: attr(data-number);
				font-size: 10px;
				font-family: $fontMuni;
				font-weight: bold;
				color: #fff;
				text-align: center;
				display: block;
				width: 16px;
				height: 16px;
				position: relative;
				top: 2px;
			}
		}
		#{$s}{
			margin-bottom: 0;
		}
		#{$s}__point{
			left: -36px;
		}
		#{$s}__list {
			#{$s}{
				display: inline-block;
				vertical-align: middle;
				width: auto;
			}
			#{$s}__list{
				&:before{
					left: 9px;
				}
				#{$s}__box{
					padding-bottom: 25px;
					margin-left: 0;
				}
				#{$s}__box__title{
					padding-bottom: 0;
					&:before{
						display: none;
					}
				}
				#{$s}__item{
					#{$s}__point{
						left: -36px;
					}
					&:first-child{
						#{$s}__point{
							&:before{
								bottom: 100%;
								margin-bottom: 3px;
								top: auto;
								margin-top: 0;
								background: #fff;
							}
						}
					}
					&:last-child{
						#{$s}__point{
							&:before{
								top: 100%;
								margin-top: 3px;
								bottom: auto;
								margin-bottom: 0;
								background: #fff;
							}
						}
					}
				}
			}
		}
	}
}

$s: '.box-timeline-progress';

.box-timeline-progress {
	margin: 0 0 $mMargin;
	text-align: center;
	overflow: hidden;

	&__list {
		@extend %reset-ol;
		@include inline-list(33.333%, $fontSizeSmall);
		margin-left: -30px;
	}
	&__item {
		@extend %reset-ol-li;
		overflow: hidden;
		box-sizing: border-box;
		padding-left: 30px;
		line-height: (22 / 14);
		&:first-child {
			#{$s}__illust:before {
				display: none;
			}
		}
		&:last-child {
			#{$s}__illust:after {
				display: none;
			}
		}
	}
	&__illust {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 80px;
		height: 80px;
		margin-bottom: 0;
		border: 7px solid #ccc;
		text-align: center;
		font-size: 32px;
		line-height: 65px;
		font-weight: 700;
		color: black;
		border-radius: 40px;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			height: 1px;
			width: 1000px;
			background: $borderPrimary;
		}
		&:before {
			right: 100%;
			margin-right: 7px;
		}
		&:after {
			left: 100%;
			margin-left: 7px;
		}

		.icon {
			top: -3px;
			font-size: 21px;
			&-check {
				top: 0;
				font-size: 32px;
			}
		}
	}
	&__title {
		@extend .h4;
		margin-bottom: .3em;
		color: black;
	}
	&__list{
		margin-left: -60px;
	}
	&__item{
		padding-left: 60px;
	}
	&__desc {
		padding: 0 30px;
	}
}

$s: '.box-timeline-dates';

.box-timeline-dates {
	margin: 0 0 $mMargin;
	.box-event {
		position: relative;
		z-index: 1;
	}
	&__box {
		padding-top: $mMarginSmall;
	}
	&__title {
		margin: 0 0 1.25em;
	}
	&__list {
		@extend %reset-ul;
		margin-bottom: 0;
	}
	&__item {
		@extend %reset-ul-li;
	}
}

$s: '.box-tabs';

.box-tabs{
	margin: 0 0 $mMargin;
	&__responsive-link {
		display: block;
		font-size: 24px;
		margin-bottom: 10px;
	}
}

$s: '.box-accordion';

.box-accordion{
	margin: 0 0 $mMargin;
	&__title {
		text-decoration: underline;
		font-weight: 400;
		display: block;
		font-size: 24px;
		margin-bottom: 10px;
	}
}

$s: '.box-vcards-list';

.box-vcards-list {
	margin: 0 0 $mMargin;
	border: 1px solid #ddd;
	&__list{
		@extend %reset-ul;
	}
	&__item{
		@extend %reset-ul-li;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 20px;
			right: 20px;
			height: 1px;
			background: #eee;
		}
		&:last-child:after {
			display: none;
		}
		&--noimg {
			#{$s}__inner {
				padding-top: 0;
				padding-bottom: 0;
			}
			#{$s}__content {
				padding: 0;
			}
		}
		+ #{$s}__item {
			margin-top: -1px;
		}
	}
	&__inner {
		padding: 0 20px;
		height: 110px;
		display: table;
		width: 100%;
		color: black;
		text-decoration: none;
		@include inline-list(auto, $fontSize);
		> * {
			display: table-cell;
			vertical-align: middle;
		}
	}
	&__img {
		margin: 0;
		width: 70px;
		img{
			max-width: none;
		}
	}
	&__name {
		font-size: 18px;
		margin: 0;
		color: black;
		.icon {
			color: black;
		}
	}
	&__position {
		font-size: 14px;
		margin: 0;
	}
	&__content {
		padding: 0 30px;
	}
	&--small {
		#{$s}__inner {
			height: 90px;
			padding: 20px;
		}
		#{$s}__content {
			padding: 0 10px;
		}
	}
	&--noimg {
		#{$s}__inner {
			padding: 30px;
		}
	}
}

$s: '.box-hero-header';

.box-hero-header {
	position: relative;
	margin-bottom: $mMarginSmall;
	min-height: 350px;
	.white {
		color: $color;
	}
	&__img,
	&__video {
		display: none;
	}
	&__content {
		position: relative;
		padding-top: 130px;
		padding-bottom: 50px;
	}
	&__content-inner {
		max-width: 515px;
		text-align: left;
		h1{
			font-family: $font;
			border: 2px solid currentColor;
			border-width: 2px 0;
			padding: 10px 0;
		}
		p{
			line-height: 22px;
			margin-bottom: 10px;
		}
	}
	&__separator{
		border-top: 2px solid currentColor;
	}
}

$s: '.box-rotate';

.box-rotate{
	margin: 0 0 $mMargin;
	&__front,
	&__back{
		display: block;
		position: relative;
		&:before{
			content: '';
			display: block;
			padding-top: 100%;
			background: #ccc;
			-webkit-print-color-adjust: exact;
			print-color-adjust: exact;
		}
	}
	&__title{
		color: $color;
	}
	&__back{
		text-decoration: none;
	}
	&__content{
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		padding: 20px 30px;
		text-align: center;
		color: $color;
		p:last-child{
			margin-bottom: 0;
		}
	}
}

$s: '.box-slider';

.box-slider {
	position: relative;
	z-index: 1;
	margin: 0 0 $mMargin;
	&__item {
		text-decoration: none;
		color: $color;
		img {
			width: 100% !important;
			height: auto !important;
		}
	}
	.slick-track {
		width: auto !important;
		transform: translate3d(0, 0, 0) !important;
		.slick-slide {
			display: none;
		}
		.slick-slide:first-child {
			display: block !important;
		}
	}
}
