@mixin sprite2x-tmp() {
	background: url('#{$img}bg/sprites-retina.png') (0 / 2)+px (0 / 2)+px no-repeat;
	background-size: (16 / 2)+px (16 / 2)+px;
}
@mixin sprite2x-tmp-size() {
	@include sprite2x-tmp();
	width: (16 / 2)+px;
	height: (16 / 2)+px;
}
@mixin sprite2x-tmp-position() {
	background-position: (0 / 2)+px (-0 / 2)+px;
}
